<template>
  <div class="wrapper">
      <div class="float-bar" >  
                <router-link to="/drama">
                    <i class="fa fa-arrow-left" aria-hidden="true"></i>                
                </router-link>
                <span class="span">{{dramaDetail.bangumi_title}}</span>
                <span class="index">({{index}})</span>
      </div>
      <video controls="controls" :src="videoSrc"  class="video"></video>                  
      <div class="Anthology">
            <span></span>
            <span class="new"> {{dramaDetail.newest_ep_index}}  ></span>
      </div>
      <div class="anthology_part">
          <ul class="list" >
              <li class="part"  v-for="item in dramaDetail.episodes" @click="showIndex(item.index)">
                      <p class="part_name" >{{item.index}}</p>
                      <p class="part_title" >{{item.index_title}}</p>
              </li>                
          </ul>
      </div>
      <div class="showInfo">
              <p class="title"></p>
              <p class="desc">{{dramaDetail.evaluate}}</p>
              <div class="tagAll"> 
                    <p class="tagAbout"></p> 
                    <span class="tag" v-for="tag in dramaDetail.tags">{{tag.tag_name}}</span>                   
              </div>
     </div>
     <div class="ShowComment" >
              <p class="comment"> <span>({{comment.length}})</span></p>
              <ul class="cmtList">
                  <li v-for="(cmt, index) in comment">
                      <img src="../assets/image/s8.png" alt="" class="avatar">
                      <span class="name">{{cmt.member.uname}}</span>
                      <span class="ico" >   
                        <i class="fa fa-thumbs-o-up" aria-hidden="true" @click="star(index)"></i>{{cmt.like}}        
                      </span>
                      <p class="comment">{{cmt.content.message}}</p>
                  </li>
              </ul>
            <div class="talk">
                <img src="../assets/image/emoji.png" class="emoji"></img>
                <input type="text" v-model="cmtContent" @keypress.enter="submitCmt"  placeholder="">
                <img src="../assets/image/send.png" class="send" @click="submitCmt">
            </div>
          </div>
  </div>
</template>

<script>
export default {
   data() {
       return {
            videoSrc: '',
            dramaDetail: '',
            comment: [ ],
            cmtContent: '',
            index: 1
       }
   },
   mounted() {
         let season_id =  this.$route.params.season_id
         this.axios.post('https://api.imjad.cn/bilibili/?season_id=' + season_id + '&page=1&quality=2&index=1').then((res) => {
                this.videoSrc = res.data.durl[0].url
             }).catch((error) => {
                console.log(error)
        })
        this.axios.post('https://api.imjad.cn/bilibili/?get=seasoninfo&season_id=' + season_id ).then((res) => {
                this.dramaDetail = res.data.result
             }).catch((error) => {
                console.log(error)
        })
        this.axios.post('https://api.imjad.cn/bilibili/?get=comments&season_id=' + season_id ).then((res) => {
                this.comment = res.data.data.replies
             }).catch((error) => {
                console.log(error)
        })
  },
  methods: {
    star(index) {
         var fa = document.querySelectorAll('.ico')     
          if(this.isStar[index]) {
                 this.comment[index].like++;
                 fa[index].style.color="#FA7198"
                 this.isStar[index] = ! this.isStar[index]
             }else {
                 this.comment[index].like--;
                 fa[index].style.color="#AFAFAF"
                 this.isStar[index] = ! this.isStar[index]
             }
     },
     submitCmt() {
        let content = this.cmtContent;
        if(content === '') {
            this.util.cmtPop()
        }else {
            this.comment.unshift({
                    rpid: 274774377,
                    oid: 62894,
                    type: 1,
                    mid: 194205,
                    root: 0,
                    parent: 0,
                    count: 0,
                    rcount: 0,
                    floor: 22,
                    state: 0,
                    attr: 0,
                    ctime: 1496467863,
                    rpid_str: "274774377",
                    root_str: "0",
                    parent_str: "0",
                    like: 0,
                    action: 0,
                    member: {
                        mid: "194205",
                        uname: "honeyvickys",
                        sex: "",
                        sign: "@",
                        avatar: "http://i0.hdslb.com/bfs/face/2c3a364cf409a85b4c651a6afbf6ffe22208c654.jpg",
                        rank: "10000",
                        DisplayRank: "0",
                        level_info: {
                            current_level: 5,
                            current_min: 10800,
                            current_exp: 27703,
                            next_exp: 28800
                        },
                        pendant: {
                            pid: 0,
                            name: "",
                            image: "",
                            expire: 0
                            },
                        nameplate: {
                            nid: 9,
                            name: "",
                            image: "http://i2.hdslb.com/bfs/face/3f2d64f048b39fb6c26f3db39df47e6080ec0f9c.png",
                            image_small: "http://i2.hdslb.com/bfs/face/90c35d41d8a19b19474d6bac672394c17b444ce8.png",
                            level: "",
                            condition: ">=50"
                        },
                        official_verify: {
                            type: -1,
                            desc: ""
                        },
                        vip: {
                            vipType: 1,
                            vipDueDate: 1496937600000,
                            dueRemark: "5",
                            accessStatus: 1,
                            vipStatus: 1,
                            vipStatusWarn: ""
                        }
                    },
                    content: {
                        message: `${content}`,
                        plat: 1,
                        device: "",
                        members: [ ]
                    },
                    replies: []
            })
        }
        this.cmtContent = ''
     },
     showIndex(index) {
         this.index = index
         let season_id =  this.$route.params.season_id
         this.axios.post('https://api.imjad.cn/bilibili/?season_id=' + season_id + '&page=1&quality=2&index=' +index).then((res) => {
                this.videoSrc = res.data.durl[0].url
             }).catch((error) => {
                console.log(error)
        })
     }
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/dramaPlay.scss';

</style>


